"use client"
import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useSession } from '../sessionProvider';
export default function LoginPage() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const router = useRouter();

  const { session, login, logout } = useSession();


  useEffect(() => {
    console.log(session);
  }, [session])
  
  async function handleSubmit(e) {
    e.preventDefault();
    setLoading(true);
    setError(null);
    console.log("non");
    try {
      console.log('test');
      const res = await fetch('http://localhost/api/login', { // proxy vers Laravel API
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email, password }),
      });

      if (!res.ok) {
        const err = await res.json();
        throw new Error(err.message || 'Erreur connexion');
      }

      const data = await res.json();
      
      // Stocke le token localement (ex: localStorage)
      localStorage.setItem('token', data.token);

      // Redirige vers la home ou dashboard
      router.push('/');
    } catch (e) {
      setError(e.message);
    } finally {
      setLoading(false);
    }
  }

  return (
    <form onSubmit={handleSubmit} style={{ maxWidth: 300, margin: 'auto' }}>
      <h1>Connexion</h1>
      <input
        type="email"
        placeholder="Email"
        value={email}
        onChange={e => setEmail(e.target.value)}
        required
        />
      <input
        type="password"
        placeholder="Mot de passe"
        value={password}
        onChange={e => setPassword(e.target.value)}
        required
        />
      <button type="submit">{loading ? 'Connexion...' : 'Se connecter'}</button>
      {error && <p style={{ color: 'red' }}>{error}</p>}
    </form>
  );
}
